import {Component, NgZone, OnInit, ViewChild} from '@angular/core';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {FormBuilder, Validators} from '@angular/forms';

@Component({
  selector: 'app-add-service',
  templateUrl: './add-service.component.html',
  styleUrls: ['./add-service.component.scss']
})
export class AddServiceComponent implements OnInit {
  @ViewChild('tplContent')tplContent;
  validateForm;
  tplForms;
  modal;
  dataSet;
  allArea;
  areaData = [];
  saveP;
  saveC;
  allSelectC = [];
  //区域联动参数
  selectedProvince = '';//当前的省
  selectedCity =[];//当前的城市
  provinceData = [];//省数组
  cityData;

  constructor(
    private fb:FormBuilder,
    private msg:NzMessageService,
    private url:UrlServeService,
    private util:UtilServeService,
    private http:ResetHttpService,
    private modalserve:NzModalService,
    private zone: NgZone
  ) {
   this.validateForm = this.fb.group({
      serviceName:['',[Validators.required]],
      authManger:['',[Validators.required]],
      phone:['',[Validators.required]],
      idCard:['',[Validators.required]],
     serveAreaName:['',[Validators.required]],
     loginUser:['',[Validators.required]],
     selectP:['',[Validators.required]],
     selectC:['',[Validators.required]]
    });
   this.tplForms = this.fb.group({
     name:['',[Validators.required]],
     auth_manager:['',[Validators.required]],
     telephone:['',[Validators.required]],
     id_card_num:['',[Validators.required]]
   })
  }

  ngOnInit() {
    this.getallCity();
    this.getAreaList();
  }
  //获取所有省市
  getallCity(){
    this.http.get(this.url.allCityList,{

    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.provinceData = data.response;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  //获取服务大区列表
  getAreaList(){
    this.http.get(this.url.areaList,{

    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.allArea = data.response;
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  commit(){
    this.getAllCityId(this.areaData);
    console.log(this.allSelectC);
    for(let key in this.validateForm.controls){
      this.validateForm.controls[key].markAsDirty();
      this.validateForm.controls[key].updateValueAndValidity();
    }
    if(!this.validateForm.valid){
      return;
    }
    let terminalJsonStr;
    if(!this.dataSet){
      terminalJsonStr = '';
    }else{
      terminalJsonStr = JSON.stringify(this.dataSet);
    }
    this.http.get(this.url.addServe,{
      name:this.validateForm.controls['serviceName'].value,
      auth_manager:this.validateForm.controls['authManger'].value,
      id_card_num:this.validateForm.controls['idCard'].value,
      telephone:this.validateForm.controls['phone'].value,
      region_id:this.validateForm.controls['serveAreaName'].value,
      address_ids:this.allSelectC.join(','),
      terminalJsonStr:terminalJsonStr,
      login_name:this.validateForm.controls['loginUser'].value
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.util.initPassword();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    });
  }
  cancle(){
    history.back();
  }
//创建模态窗 添加服务终端
  createModal(): void {
    this.tplForms.reset();
    this.modal = this.modalserve.create({
      nzTitle: '',
      nzContent: this.tplContent,
      nzClosable: false,
      nzFooter:null
    });
  }
  tplcommit($event,value){
    for(let key in this.tplForms.controls){
      this.tplForms.controls[key].markAsDirty();
      this.tplForms.controls[key].updateValueAndValidity();
    }
    if(!this.tplForms.valid){
      return;
    }
    if(!this.dataSet){
      this.dataSet = [];
    }
    this.zone.run(() => {
      this.dataSet = [ ...this.dataSet, value ];
    });
    this.modal.destroy();

  }
  tplcancle(){
    this.modal.destroy();
  }

  //区域联动

  provinceChange(value): void {
    //如果没有旧的创建
    if(value){
      if(value.length == 0){
        this.saveP = {
          id:this.selectedProvince,
          name:'',
          children:[]
        };
        this.saveC = [];
        this.areaData.push(this.saveP);
      }else{
        //如果当前的与旧的不同 创建
        this.areaData.forEach((v,k)=>{
          if(v.id == this.selectedProvince){
            this.areaData.splice(k,1);
          }
        });
        //清空
        this.selectedCity = [];
        this.saveP = {
          id:this.selectedProvince,
          name:'',
          children:[]
        };
        this.saveC = [];
        this.areaData.push(this.saveP);
      }
    }

    //联动
    this.provinceData.forEach((v,k) =>{
      if(v['id'] == value){
        this.cityData = v['children'];
      }
    })
  }
  cityChange(value){
    console.log(value);
    if(value){
      if(value.length >= 0){
        this.saveC = [];
        //存储城市
        var nowC = {
          id:'',
          name:''
        };
        this.selectedCity.forEach((v,k)=>{
          nowC.id = v;
          this.saveC.push(nowC);
        });
        this.saveP.children = this.saveC;
        //转换成表格数据
        this.provinceData.forEach((v,k)=>{
          if(v.id == this.saveP.id){
            this.saveP.name = v.name;
          }
        });
      }
    }
    console.log(this.areaData)
  }
  //获取当前所有的城市
  getAllCityId(arr){
    this.allSelectC = [];
    arr.forEach((v,k)=>{
      v['children'].forEach((m,n)=>{
        this.allSelectC.push(m.id);
      });
    });
  }
}
