import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {NzMessageService} from 'ng-zorro-antd';

@Component({
  selector: 'app-add-servece-area',
  templateUrl: './add-servece-area.component.html',
  styleUrls: ['./add-servece-area.component.scss']
})
export class AddServeceAreaComponent implements OnInit {
  profileForm;
  constructor(
    private url:UrlServeService,
    private util:UtilServeService,
    private http:ResetHttpService,
    private msg:NzMessageService,
    private fb:FormBuilder
  ) {
   this.profileForm = fb.group({
     areaName:['',[Validators.required]]
   })
  }

  ngOnInit() {

  }
  get areaName(){
    return this.profileForm.controls['areaName'];
  }
  submitForm(){
    this.areaName.markAsDirty();
    this.areaName.updateValueAndValidity();
    if(!this.areaName.valid){
      return;
    }
    this.http.get(this.url.addArea,{
      name:this.areaName.value
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('添加成功！');
        history.back();
      }else{
        this.msg.error(data.message);
      }
    },err=>{
      console.log(err);
    })
  }
  cancle(){
    history.back();
  }
}
