import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ViewServiceComponent} from './view-service/view-service.component';
import {AddServiceComponent} from './add-service/add-service.component';
import {ServiceMangerComponent} from './service-manger/service-manger.component';
import {EditServeceAreaComponent} from './edit-servece-area/edit-servece-area.component';
import {AddServeceAreaComponent} from './add-servece-area/add-servece-area.component';
import {ServeceAreaMangerComponent} from './servece-area-manger/servece-area-manger.component';

const routes: Routes = [
  {path: '', component:ServiceMangerComponent,data:{breadcrumb: '服务商管理'}},
  {path: 'add', component:AddServiceComponent,data:{breadcrumb: '添加服务商'}},
  {path: 'view', component:ViewServiceComponent,data:{breadcrumb: '查看服务商'}},
  {path: 'area', component:ServeceAreaMangerComponent,data:{breadcrumb: '服务大区'}},
  {path:'add-area',component:AddServeceAreaComponent,data:{breadcrumb:'新增服务大区'}},
  {path:'edit-area',component:EditServeceAreaComponent,data:{breadcrumb:'编辑服务大区'}}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServiceRoutingModule { }
