import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ModalEditPasswordComponent} from '../../user/modal-edit-password/modal-edit-password.component';
import {FormBuilder} from '@angular/forms';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {ModalResetPasswordServiceComponent} from '../modal-reset-password-service/modal-reset-password-service.component';

@Component({
  selector: 'app-service-manger',
  templateUrl: './service-manger.component.html',
  styleUrls: ['./service-manger.component.scss']
})
export class ServiceMangerComponent implements OnInit {
  validateForm;
  i = 1;
  isEnablemodal;
  isloading = true;
  dataSet = [
    {status:'Y'},
    {status:'N'},
    {status:'Y'}
  ];
  allId:any = [];
  constructor(private fb:FormBuilder,
              private msg:NzMessageService,
              private modalService:NzModalService,private router:Router,private url :UrlServeService,private http:ResetHttpService,private util:UtilServeService) {
    this.validateForm = this.fb.group({
      state:[],
      phone:[],

    })
  }
  //获取组件
  get state(){
    return this.validateForm.controls.state;
  }get phone(){
    return this.validateForm.controls.phone;
  }

  ngOnInit() {
    this.getList();
    this.isloading = false;
  }
  //页面跳转
  goPager(option){
    this.util.goPage(option);
  }
  //搜索
  searchMore(){
    console.log(this.state.value,this.phone.value);
    this.getList(this.phone.value,this.state.value);

  }
  //获取服务商列表
  getList(name ='',status = ''){
    this.isloading =true;
    this.http.get(this.url.serveList,{
      page:'',
      status:status,
      search_param:name,
      rows:''
    },data=>{
      this.dataSet = data.response;
      this.isloading = false;
      this.dataSet.forEach((v,k)=>{
        this.allId.push(v['id']);
      });
      // console.log(data);
    },err=>{
      console.log(err);
    })
  }

  /*
  * 表格操作
  * */

  //停用
  isEnable(data){
    // console.log(data);
    this.isEnablemodal = this.modalService.create({
      nzTitle: '',
      nzContent: '停用后，该账户将无法登录服务商后台，并无法处理用户订单！<br><br>确认停用吗',
      nzClosable: false,
      nzOnOk: () => new Promise((resolve) => {
        this.http.get(this.url.isOpenServe,{
          id:data.id,
          status:'N'
        },res=>{
          // console.log(data);
            if(res.code === '001000'){
              this.getList();
              this.msg.success('修改完成');
              this.isEnablemodal.nzOkLoading = false;
              this.isEnablemodal.destroy();
            }else{
              console.log(data.message);
              this.isEnablemodal.destroy();
            }
        },err=>{
          console.log(err);
        });
      }),
      nzOnCancel:() => new Promise((resolve) =>{
        this.isEnablemodal.nzCancelLoading =false;
        this.isEnablemodal.destroy();
      })
    });
  }
  //启用
  isable(data){
    this.http.get(this.url.isOpenServe,{
      id:data.id,
      status:'Y'
    },data=>{
      // console.log(data);
      if(data.code === '001000'){
        this.getList();
        this.msg.success('修改完成')
      }else{
        console.log(data.message);
      }
    },err=>{
      console.log(err);
    })

  }
  //创建模态窗 重置密码
  createModal(data): void {
    this.validateForm.reset();
    var modal = this.modalService.create({
      nzTitle: '重置密码',
      nzContent: ModalResetPasswordServiceComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));

  }
}
